.footer{
    height: 15vh;
    width: 100%;
    background-color: orange;
    padding: 1rem 0;
    @media (min-width:1024px) {
        height: 10vh;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__nav{
        background-color:rgb(174, 3, 3);
        margin: 0 10%;
        height: 50%;
        @media (min-width:1024px){
            height: 60%;
            width: 30%;
            margin-left:10%;
        }
        &-list{
            display: flex;
            justify-content:space-around;
            align-items: center;
            height: 100%;          
        }
        &-link{
            color: white;
            @media (min-width:1024px) {
                position: relative;

                &:before {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 2px;
                    bottom: 0;
                    margin: -5px 0;
                    background-color: white;
                    visibility: hidden;
                    transform: scaleX(0);
                    transition: all 0.4s ease-in-out 0s;
                }

                &:hover:before {
                    visibility: visible;
                    transform: scaleX(1);
                }
            }
        }
    }

    &__networks{
        background-color: rgb(98, 51, 18);
        margin: 0 25%;
        height: 50%;
        @media (min-width:1024px) {
            height: 60%;
            width: 10%;
            margin-right: 7%;
        }
        &-list {
            height: 100%;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
        }
        &-link{
            color: white;
            @media (min-width:1024px) {
                position: relative;
                &:before {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 2px;
                    bottom: 0;
                    margin: -5px 0;
                    background-color: white;
                    visibility: hidden;
                    transform: scaleX(0);
                    transition: all 0.4s ease-in-out 0s;
                }
            
                &:hover:before {
                    visibility: visible;
                    transform: scaleX(1);
                }
            }
        }
    }    
}


